.section-article {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 70%;
    margin-bottom: 150px;
    
}

.article_appear {
  opacity: 0;
  transform: translateY(50px);
  transition: opacity 2s ease, transform 3s ease;
}

.article_appear.show {
  opacity: 1;
  transform: translateY(0);
}


.text-article {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
    width: 50%;
}

.title-article {
    font-size: 50px;
    width: 100%;
    margin-bottom: 10px;
    color: #003193;
    background: linear-gradient(90deg, #003193, #256dfc);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
}

.text-div {
    font-size: 20px;
    width: 100%;
    color: #7b7b7b;
}
